.container {
  background-image: url("../../images/profitCalculator/calc-bg.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 1400px auto;
  height: 656px;
  padding-top: 100px;

  .title {

    p:first-of-type {
      h2 {
        font-weight: 500;
        font-size: 30px;
        line-height: 35px;
        text-align: center;
        color: #FFFFFF;
        text-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
        padding-bottom: 8px; }
      h4 {
        font-size: 18px;
        line-height: 26px;
        text-align: center;
        color: #FFFFFF;
        padding-bottom: 42px; } }

    p:last-of-type {
      position: relative;
      bottom: 170px;
      left: 1290px;

      display: flex;
      flex-direction: column;
      justify-content: center;

      text-align: center;
      width: 148px;
      height: 148px;
      background: #FFFFFF;
      box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.11);
      border-radius: 50%;
      &:after {
        content: '';
        width: 198px;
        height: 198px;
        border: 1px solid #FFF;
        position: absolute;
        border-radius: 50%;
        margin: 0 0 0 -30px; }

      &:before {
        position: absolute;
        content: "";
        width: 232px;
        height: 232px;
        border: 1px solid rgba(255, 255, 255, 0.4);
        border-radius: 50%;
        margin: 0 0 0 -50px; }

      span:first-of-type {
        font-size: 15px;
        line-height: 18px;
        color: #116641; }

      span:last-of-type {
        font-weight: bold;
        font-size: 24px;
        line-height: 28px;
        color: #116641; } } } }
